.container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  background-color: var(--background-color);

  & .column {
    flex-direction: column;
  }

  & .row {
    flex-direction: row;
  }

  & .content {
    display: flex;
    align-items: center;
    justify-content: center;
    position: static;
    max-width: 700px;
    gap: 16px;

    @media (max-width: 768px) {
      flex-direction: column;
      max-width: 100%;

      padding: 0 16px;
    }
  }

  & h1 {
    color: var(--heading-color);
    font-size: 60px;
    line-height: 64px;
    gap: 16px;

    @media (max-width: 768px) {
      text-align: center;
      font-size: 40px;
      line-height: 44px;
    }
  }

  & p {
    color: var(--text-color);
    font-size: 20px;
    line-height: 24px;
    margin-top: 16px;
    width: 80%;
    text-align: center;

    @media (max-width: 768px) {
      width: 100%;
      text-align: center;
    }
  }

  & .md {
    font-size: 64px;
    line-height: 64px;

    @media (max-width: 768px) {
      font-size: 48px;
      line-height: 52px;
    }
  }

  & .lg {
    font-size: 128px;
    line-height: 128px;

    @media (max-width: 768px) {
      font-size: 96px;
      line-height: 100px;
    }
  }

  & .targetContainer {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
    height: 100vh;

    &>:first-child {
      opacity: 0;
      overflow: hidden;
      text-transform: uppercase;
      text-align: center;
      font-weight: bold;
      color: #fff;
      animation: smoothAppear 1s ease-in forwards, glitch 2s steps(100) infinite;

      &:hover {
        cursor: pointer;
      }
    }
  }
}

@keyframes smoothAppear {
  0% {
    opacity: 0.5;
    width: 0px;
    color: var(--background-color);
  }

  50% {
    opacity: 1;
    color: var(--accent-color);
  }

  75% {
    opacity: 1;
    color: var(--secondary-color);
  }

  100% {
    opacity: 1;
    width: 100%;
    color: var(--heading-color);
  }
}

